@keyframes width {
  0% {
    opacity: 0;
    transform: translateX(0);
  }
  10% {
    opacity: 0.7;
  }
  90% {
    opacity: 0;
  }
  100% {
    transform: translateX(-45%);
  }
}
@keyframes show-section {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes height-block {
  0% {
    height: 0;
  }
  100% {
    height: 100%;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
ul {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}
img {
  object-fit: contain;
}
li {
  list-style: none;
}
.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
.menu {
  display: flex;
  margin: 32px -8px 0px 0;
}
@media (max-width: 1250px) {
  .menu {
    display: none;
  }
}
.menu__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
}
.menu__link {
  color: #F2E1C2;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 19.5px;
  text-transform: uppercase;
  transition: 0.5s ease;
}
.menu__link:hover {
  color: #F23207;
}
.menu__list--mobile .menu__link {
  color: #262526;
}
.menu__popup {
  background-color: #F2E1C2;
  padding: 10px 15px 10px 15px;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  min-width: 179px;
  transition: 0.5s ease;
}
.menu__popup:hover {
  background-color: #F23207;
}
.menu__popup:hover .menu__sign {
  color: #F2E1C2;
}
.menu__sign {
  color: #262526;
  font-weight: 600;
  font-size: 16px;
  line-height: 19.5px;
  text-transform: uppercase;
}
.menu__logo {
  display: block;
}
.menu__block {
  width: 440px;
}
.header {
  z-index: 2;
}
.slider {
  margin: 119px 0 110px 0;
  position: relative;
  z-index: 2;
}
.slider__heading {
  font-weight: 500;
  font-size: 36px;
  line-height: 43.88px;
  margin: 15px 0 15px 0;
  color: #F23207;
  text-transform: uppercase;
  animation: show-section 1s linear 0s 1 forwards;
  opacity: 0;
}
.slider__list {
  animation: show-section 1s ease-in 1.5s 1 forwards;
  opacity: 0;
}
.slider__img {
  position: absolute;
  left: 0;
  top: 0;
}
.slider__video {
  width: 100%;
  height: 525px;
  object-fit: cover;
}
@media (max-width: 1250px) {
  .slider__video {
    height: auto;
  }
}
.slider__position {
  display: flex;
  flex-direction: column;
  position: relative;
  clip-path: url(#clip-mars);
}
.slider__position .clip-path-svg {
  height: 0;
}
.slider__background {
  background-image: url("../img/background.png");
}
.slider__wrapper {
  display: flex;
  flex-direction: column;
  padding: 0 0 0 46px;
  position: relative;
}
@media (max-width: 1250px) {
  .slider__wrapper {
    padding: 0 20px 0 20px;
  }
}
.slider__animation {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
}
.slider__circle {
  display: block;
  background-color: #FFFFFF;
  border-radius: 50%;
  width: 6px;
  height: 6px;
}
.slider__line {
  display: block;
  width: 2px;
  height: 0;
  background-color: #FFFFFF;
  animation: height-block 4s linear 0s 1 forwards;
}
.slider__circle--bottom {
  opacity: 0;
  animation: show-section 6s linear 1s 1 forwards;
}
.main__background {
  background-image: url('../img/background.png');
  background-size: contain;
  width: 105%;
  height: 100%;
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
}
.body {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden;
}
@media (max-width: 1250px) {
  .body {
    overflow-x: unset;
  }
}
.body::before {
  content: "";
  background-image: url("../img/perls__noise.png");
  position: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 300%;
  height: 100%;
  top: 0;
  left: 0;
  animation: width 8s ease-out 2s infinite;
  z-index: 1;
}
.body__scroll {
  overflow-y: hidden;
}
.form {
  background-color: #F2E1C2;
  position: fixed;
  height: 0;
  width: 100%;
  top: 0;
  z-index: 3;
  opacity: 0;
  pointer-events: none;
  overflow-y: hidden;
}
.form__open {
  height: 100%;
  transition: height 0.7s ease, opacity 0.7s ease;
  opacity: 1;
  pointer-events: all;
  overflow-y: scroll;
  z-index: 6;
}
.form__close {
  height: 0;
  transition: height 0.7s ease, opacity 0.7s ease;
  opacity: 0;
  pointer-events: none;
  overflow-y: hidden;
}
.form__heading {
  font-family: 'Bebas Neue', sans-serif;
  font-weight: 700;
  font-size: 114px;
  line-height: 114px;
  margin: 100px 0 80px 0;
  text-transform: uppercase;
  letter-spacing: 4.25px;
}
@media (max-width: 1250px) {
  .form__heading {
    font-size: 20px;
    line-height: 20px;
    margin: 20px 0 20px 0;
  }
}
.form__button {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  background-color: #262526;
  width: 32px;
  height: 32px;
  position: relative;
}
.form__button::before,
.form__button::after {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: #FFFFFF;
}
.form__button::after {
  transform: rotate(45deg);
}
.form__button::before {
  transform: rotate(-45deg);
  margin-bottom: -2px;
}
.form__button:hover::before,
.form__button:hover::after {
  transform: rotate(180deg);
  transition: all 0.3s ease-out;
}
.form__list {
  display: flex;
}
.form__list--flex {
  align-items: center;
}
@media (max-width: 1250px) {
  .form__list--flex {
    align-items: flex-start;
    flex-direction: row;
  }
}
.form__item--flex {
  flex: 1;
}
.form__item--flex:last-child {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 170px;
}
@media (max-width: 1250px) {
  .form__item--flex:last-child {
    margin-bottom: 50px;
    margin-top: 20px;
  }
}
.form__item--column {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-right: 20px;
}
.form__item--column:last-child {
  margin-right: 0;
}
@media (max-width: 1250px) {
  .form__item--column {
    margin-top: 30px;
  }
}
.form__date {
  font-weight: 700;
  font-size: 16px;
  line-height: 19.5px;
  margin-bottom: 20px;
}
.form__input {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #262526;
  background-color: transparent;
  outline: none;
  padding: 0px 0 10px 0;
}
.form__input::-webkit-calendar-picker-indicator {
  width: 80%;
  opacity: 0;
}
@media (max-width: 1250px) {
  .form__input::-webkit-calendar-picker-indicator {
    width: 60%;
  }
}
.form__sign {
  font-weight: 600;
  font-size: 16px;
  line-height: 19.5px;
  color: #F2E1C2;
  text-transform: uppercase;
}
.form__send {
  cursor: pointer;
  width: 100%;
  border-radius: 4px;
  border: none;
  background-color: #262526;
  padding: 10px 0 10px 0;
  margin-top: 64px;
  margin-left: 8px;
  transition: 0.5s ease;
}
.form__send:hover {
  background-color: #FFFFFF;
}
.form__send:hover .form__sign {
  color: #262526;
}
@media (max-width: 1250px) {
  .form__send {
    margin-top: 30px;
    margin-bottom: 30px;
  }
}
@media (max-width: 1250px) {
  .form__wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    padding: 0 20px 0 20px;
  }
}
@media (max-width: 1250px) {
  .form__block .form__list {
    flex-direction: column;
  }
}
.burger-menu__button {
  justify-content: center;
  user-select: none;
  margin: 0;
  border-radius: 0;
  font-size: 0;
  line-height: 0;
  outline: 0;
  cursor: pointer;
  box-sizing: content-box;
  width: auto;
  padding: 10px;
  padding-right: 0;
  background: 0 0;
  border: 0;
}
@media (min-width: 1250px) {
  .burger-menu__button {
    display: none;
  }
}
.burger-menu {
  display: flex;
  position: relative;
}
@media (min-width: 1250px) {
  .burger-menu {
    display: none;
  }
}
.burger-menu .menu__list {
  left: 100%;
  display: none;
}
.burger-menu .menu__list--open {
  display: block;
  height: unset;
  left: 0;
  overflow: unset;
  transition: left 0.5s ease;
  opacity: 1;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  z-index: 5;
  position: absolute;
  background-color: #FFFFFF;
  width: 100%;
}
.burger-menu .menu__list--open .menu__item {
  padding: 20px;
}
.burger-menu__list {
  padding: 30px 16px 0 16px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1250px) {
  .burger-menu__list {
    width: 100%;
  }
}
.burger-menu__toggle {
  position: relative;
  display: inline-block;
  width: 26px;
  height: 2px;
  vertical-align: bottom;
  transition: background 0.7s 0.7s;
  background-color: #F23207;
}
.burger-menu__toggle::before,
.burger-menu__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #F23207;
  transition: top 0.5s 0.5s, transform 0.5s, -webkit-transform 0.5s;
  transform-origin: 50% 50%;
}
.burger-menu__toggle::before {
  top: 8px;
}
.burger-menu__toggle::after {
  top: -8px;
}
.burger-menu__burger--open .burger-menu__toggle {
  background: 0 0;
  transition: background 0.1s 0.1s;
  z-index: 6;
}
.burger-menu__burger--open .burger-menu__toggle::after,
.burger-menu__burger--open .burger-menu__toggle::before {
  transition: top 0.3s, transform 0.3s 0.5s;
  width: 28px;
  top: 0;
  left: -5px;
}
.burger-menu__burger--open .burger-menu__toggle::before {
  transform: rotate3d(0, 0, 1, 45deg);
  background-color: #F23207;
}
.burger-menu__burger--open .burger-menu__toggle::after {
  transform: rotate3d(0, 0, 1, -45deg);
  background-color: #F23207;
}
@media (max-width: 1250px) {
  .burger-menu__nav {
    display: block;
    width: 100%;
  }
}
